
// import required modules
import { Navigation } from 'swiper'; //navigation modules import
import { Pagination } from 'swiper'; //pagination modules import
// Import Swiper styles

import './css/section_01.css'
// 
import ReactPlayer from 'react-player/lazy';

//  <Player src='https://amkiri.hgodo.com/video/yogico/yogico.mp4' width="100%" loop="true"autoPlay isFullscreen/>

function Visual(){
    return (
     <>
     <seciton class="p_l_d_b">
        <div className='player-wrapper' style={{position:'relative',zIndex:'3'}}>
                <ReactPlayer
                    className='react-player'
                    url={'https://amkiri.hgodo.com/video/yogico/yogico.mp4'}    //  url
                    width='100%'         //   ()
                    height='100%'        //   ()
                    playing={true}        //   on
                    muted={true}          //   on
                    controls={false}       //    
                    light={false}         //  
                    pip={false}            // pip   
                />
            </div>  
      </seciton>
     </>
    )
  }

  
  export default Visual
  